import React, { useState, useEffect } from "react";
import jwt_decode from "jwt-decode";
import axios from "axios";
import Swal from "sweetalert2";

// reactstrap components
import {
  Button,
  Container,
  Row,
  Col,
  Modal,
  ModalHeader,
  ModalBody,
  ModalFooter,
  Input,
  InputGroupAddon,
  InputGroupText,
  InputGroup,
  FormGroup,
} from "reactstrap";

const UserHeader = (props) => {
  const [modal, setModal] = useState(false);

  const toggle = () => setModal(!modal);

  const { buttonLabel, className } = props;

  const decode = jwt_decode(localStorage.getItem("token"));

  const [nama, setNama] = useState("");
  const [telp, setTelp] = useState("");

  const changeNama = (e) => {
    const value = e.target.value;
    setNama(value);
  };

  const changeTelp = (e) => {
    const value = e.target.value;
    setTelp(value);
  };

  const email = jwt_decode(localStorage.getItem("token")).email;
  useEffect(() => {
    async function fetchData() {
      // You can await here
      await axios
        .post(
          "http://server.clan486.my.id:54721/member",
          { email: email },
          {
            headers: {
              Accept: `application/json`,
            },
          }
        )
        .then((data) => {
          setNama(data.data.member.nama);
          setTelp(data.data.member.telp);
        });
    }
    fetchData();
  }, []);

  const update = (e) => {
    console.log(decode.memberId);
    const data = {
      memberId: decode.memberId,
      nama: nama,
      telp: telp,
    };
    console.log(data);
    axios
      .post("http://server.clan486.my.id:54721/member/update", data, {
        headers: {
          Authorization: `Bearer ${localStorage.getItem("token")}`,
          Accept: `application/json`,
        },
      })
      .then((res) => {
        console.log(res);
        Swal.fire({
          title: "Update Success",
          icon: "success",
          confirmButtonText: "Done",
        });
      });
  };

  // console.log(member);

  return (
    <>
      <div
        className="header pb-8 pt-5 pt-lg-8 d-flex align-items-center"
        style={{
          minHeight: "600px",
          backgroundImage:
            "url(" +
            require("../../assets/img/theme/profile-cover.png").default +
            ")",
          backgroundSize: "cover",
          backgroundPosition: "center top",
        }}
      >
        {/* Mask */}
        <span className="mask bg-gradient-default opacity-8" />
        {/* Header container */}
        <Container className="d-flex align-items-center" fluid>
          <Row>
            <Col lg="7" md="10">
              <h1 className="display-2 text-white">Hello {nama}</h1>
              <p className="text-white mt-0 mb-5">
                This is your profile page. You can see your profile in the
                profile menu
              </p>
              <Button color="info" onClick={toggle}>
                Edit profile
                <Modal isOpen={modal} toggle={toggle} className={className}>
                  <ModalHeader toggle={toggle}>Edit Profile</ModalHeader>
                  <ModalBody>
                    <FormGroup>
                      <InputGroup className="input-group-alternative mb-3">
                        <InputGroupAddon addonType="prepend">
                          <InputGroupText></InputGroupText>
                        </InputGroupAddon>
                        <Input
                          placeholder={nama}
                          type="text"
                          onChange={changeNama}
                        />
                      </InputGroup>
                      <InputGroup className="input-group-alternative mb-3">
                        <InputGroupAddon addonType="prepend">
                          <InputGroupText></InputGroupText>
                        </InputGroupAddon>
                        <Input
                          placeholder={telp}
                          type="text"
                          onChange={changeTelp}
                        />
                      </InputGroup>
                    </FormGroup>
                  </ModalBody>
                  <ModalFooter>
                    <Button color="primary" onClick={update}>
                      Save
                    </Button>{" "}
                    <Button color="secondary" onClick={toggle}>
                      Cancel
                    </Button>
                  </ModalFooter>
                </Modal>
              </Button>
            </Col>
          </Row>
        </Container>
      </div>
    </>
  );
};

export default UserHeader;
