import React, { Component } from "react";
import { Container, Row, Button } from "reactstrap";
import { Redirect } from "react-router-dom";
import Member from "./Member";
import Header from "components/Headers/Header.js";
import axios from "axios";
import Swal from "sweetalert2";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faTrash } from "@fortawesome/free-solid-svg-icons";

function hapus(id) {
  const data = {
    memberId: id,
  };
  console.log(id);
  axios
    .post("http://server.clan486.my.id:54721/member/delete", data, {
      headers: {
        Authorization: `Bearer ${localStorage.getItem("token")}`,
        Accept: `application/json`,
      },
    })
    .then((res) => {
      console.log(res);
      Swal.fire({
        title: "Delete Success",
        icon: "success",
        confirmButtonText: "Done",
      });
    })
    .catch((err) => console.log(err));
}

function element(id) {
  return (
    <div>
      <Button color="aqua" className="mr-2" onClick={() => hapus(id)}>
        <FontAwesomeIcon icon={faTrash} />
        Delete
      </Button>
    </div>
  );
}

export class Tbl extends Component {
  state = {
    users: [],
  };

  render() {
    const token = localStorage.getItem("token");
    if (!token) {
      <Redirect to="/auth/login" />;
    }

    axios.get("http://server.clan486.my.id:54721/member").then((res) => {
      this.setState({
        users: res.data.member.map((data) => ({
          nama: data.nama,
          nim: data.nim,
          prodi: data.prodi.prodi,
          email: data.email,
          telp: data.telp,
          action: element(data._id),
        })),
      });
    });
    return (
      <>
        <Header />
        {/* Page content */}
        <Container className="mt-7" fluid>
          <Row>
            <div>
              <Member users={this.state.users} />
            </div>
          </Row>
        </Container>
      </>
    );
  }
}
