import axios from "axios";
import React, { useState, useEffect } from "react";
import Swal from "sweetalert2";

// reactstrap components
import {
  Button,
  Card,
  CardBody,
  FormGroup,
  Form,
  Input,
  InputGroupAddon,
  InputGroupText,
  InputGroup,
  Col,
} from "reactstrap";

const Register = () => {
  const [nama, setNama] = useState("");
  const [nim, setNim] = useState("");
  const [email, setEmail] = useState("");
  const [prodi, setProdi] = useState("");
  const [notelp, setNotelp] = useState("");
  const [password, setPassword] = useState("");

  const changeNama = (e) => {
    const value = e.target.value;
    setNama(value);
  };

  const changeNim = (e) => {
    const value = e.target.value;
    setNim(value);
  };

  const changeProdi = (e) => {
    const value = e.target.value;
    setProdi(value);
  };

  const changeEmail = (e) => {
    const value = e.target.value;
    setEmail(value);
  };

  const changeNotelp = (e) => {
    const value = e.target.value;
    setNotelp(value);
  };

  const changePassword = (e) => {
    const value = e.target.value;
    setPassword(value);
  };

  const [prodis, setProdis] = useState();

  useEffect(() => {
    async function fetchData() {
      // You can await here
      const response = await axios("http://server.clan486.my.id:54721/prodi");
      setProdis(
        response.data.Prodis.map((data) => (
          <option key={data._id} value={data._id}>
            {data.prodi}
          </option>
        ))
      );
    }
    fetchData();
  }, []);

  const daftar = (e) => {
    const data = {
      nama: nama,
      nim: nim,
      prodi: prodi,
      email: email,
      telp: notelp,
      password: password,
    };
    // console.log(data);
    axios
      .post("http://server.clan486.my.id:54721/member/signup", data)
      .then((res) => {
        // alrt
        Swal.fire({
          title: "Register Success",
          icon: "success",
          confirmButtonText: "Done",
        });
        console.log(res);
      })
      .catch((err) => console.log(err));
  };

  return (
    <>
      <Col lg="6" md="8">
        <Card className="bg-secondary shadow border-0">
          <CardBody className="px-lg-5 py-lg-5">
            <div className="text-center text-muted mb-4">
              <med>SIGN UP MEMBER</med>
            </div>
            <Form role="form">
              <FormGroup>
                <InputGroup className="input-group-alternative mb-3">
                  <InputGroupAddon addonType="prepend">
                    <InputGroupText>
                      <i className="ni ni-hat-3" />
                    </InputGroupText>
                  </InputGroupAddon>
                  <Input
                    placeholder="Nama"
                    type="text"
                    value={nama}
                    onChange={changeNama}
                    required
                  />
                </InputGroup>
              </FormGroup>
              <FormGroup>
                <InputGroup className="input-group-alternative mb-3">
                  <InputGroupAddon addonType="prepend">
                    <InputGroupText>
                      <i className="ni ni-badge" />
                    </InputGroupText>
                  </InputGroupAddon>
                  <Input
                    placeholder="NIM"
                    type="text"
                    value={nim}
                    onChange={changeNim}
                    required
                  />
                </InputGroup>
              </FormGroup>
              <FormGroup>
                <InputGroup className="input-group-alternative mb-3">
                  <InputGroupAddon addonType="prepend">
                    <InputGroupText>
                      <i className="ni ni-email-83" />
                    </InputGroupText>
                  </InputGroupAddon>
                  <Input
                    placeholder="Email"
                    type="email"
                    autoComplete="new-email"
                    value={email}
                    onChange={changeEmail}
                    required
                  />
                </InputGroup>
              </FormGroup>
              <FormGroup>
                <Input
                  className="input-group-alternative"
                  type="select"
                  name="select"
                  id="exampleSelect"
                  onChange={changeProdi}
                >
                  <option selected disabled>
                    Prodi
                  </option>
                  {/* <option value="S1 - Sistem Informasi">S1 - Sistem Informasi</option>
                  <option value="S1 - Teknologi Informasi">S1 - Teknologi Informasi</option> */}
                  {prodis}
                </Input>
              </FormGroup>
              <FormGroup>
                <InputGroup className="input-group-alternative mb-3">
                  <InputGroupAddon addonType="prepend">
                    <InputGroupText>
                      <i className="ni ni-chat-round" />
                    </InputGroupText>
                  </InputGroupAddon>
                  <Input
                    placeholder="No Telp"
                    type="text"
                    value={notelp}
                    onChange={changeNotelp}
                    required
                  />
                </InputGroup>
              </FormGroup>
              <FormGroup>
                <InputGroup className="input-group-alternative">
                  <InputGroupAddon addonType="prepend">
                    <InputGroupText>
                      <i className="ni ni-lock-circle-open" />
                    </InputGroupText>
                  </InputGroupAddon>
                  <Input
                    placeholder="Password"
                    type="password"
                    autoComplete="new-password"
                    value={password}
                    onChange={changePassword}
                    required
                  />
                </InputGroup>
              </FormGroup>
              <div className="text-center">
                <Button
                  className="mt-4"
                  color="primary"
                  type="button"
                  onClick={daftar}
                >
                  Create account
                </Button>
              </div>
            </Form>
          </CardBody>
        </Card>
      </Col>
    </>
  );
};

export default Register;
