import React, { useState, useEffect } from "react";
import { Redirect } from "react-router-dom";
// jwt decode
import jwt_decode from "jwt-decode";
import axios from 'axios';

// reactstrap components
import {
  Button,
  Card,
  CardHeader,
  CardBody,
  FormGroup,
  Form,
  Input,
  Container,
  Row,
  Col,
} from "reactstrap";
// core components
import UserHeader from "components/Headers/UserHeader.js";


const Profile = () => {

const [nama, setNama] = useState('');
const [prodi, setProdi] = useState('');
const [telp, setTelp] = useState('');

  const email = jwt_decode(localStorage.getItem('token')).email;
  useEffect(() => {
    async function fetchData() {
      // You can await here
      await axios.post('http://server.clan486.my.id:54721/member', {email: email})
        .then(data => {
          setNama(data.data.member.nama);
          setProdi(data.data.member.prodi);
          setTelp(data.data.member.telp);
        })
    }
    fetchData();
  }, []);

// console.log(member);
  const token = localStorage.getItem('token')
  if(!token) {
    return <Redirect to="/auth/login" />
  }

  return (
    
    <>
      <UserHeader />
      {console.log(nama)}
      {/* Page content */}
      <Container className="mt--7" fluid>
        <Row>
          <Col className="order-xl-2 mb-5 mb-xl-0" xl="4">
            <Card className="card-profile shadow">
              <Row className="justify-content-center">
                <Col className="order-lg-2" lg="3">
                  <div className="card-profile-image">
                    <a href="#pablo" onClick={(e) => e.preventDefault()}>
                      <img
                        alt="..."
                        className="rounded-circle"
                        src={
                          require("../../assets/img/theme/team-4-800x800.jpg")
                            .default
                        }
                      />
                    </a>
                  </div>
                </Col>
              </Row>
              <CardHeader className="text-center border-0 pt-8 pt-md-4 pb-0 pb-md-4 mt-6">
                <div className="d-flex justify-content-between">
                </div>
              </CardHeader>
              <CardBody className="pt-0 pt-md-4">
                <div className="text-center">
                  <h3>
                    {nama}
                  </h3>
                  <div className="h5 font-weight-300">
                    <i className="ni location_pin mr-2" />
                    0{telp}
                  </div>
                  <div className="h5 mt-4">
                    <i className="ni business_briefcase-24 mr-2" />
                    {prodi}
                  </div>
                  <div>
                    <i className="ni education_hat mr-2" />
                    STMIK PPKIA Pradnya Paramita
                  </div>
                  <hr className="my-4" />
                  <p>
                    MEMBER UKM CLAN486
                  </p>
                </div>
              </CardBody>
            </Card>
          </Col>
        </Row>
      </Container>
    </>
  );
};

export default Profile;
